.icon {
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 100%;
  // aspect-ratio: 1 / 1;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translate(0, -50%);
    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;

    transition: all 0.3s;
  }

  &:hover &__tooltip {
    opacity: 1;
    pointer-events: all;
  }

  &--menu {
    background-image: url(../images/menu.svg);
    // background-size: cover;
    transition: all 0.3s;
    // margin-left: 30px;

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
  }
}
