.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include onTablet {
    grid-template-columns: repeat($gridColTablet, 1fr);
    column-gap: 24px;
  }

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }

  // &--desktop {
  //   display: block;
  //   @include onDesktop {
  //     display: grid;
  //   }
  // }

  // &--tablet {
  //   display: block;
  //   @include onTablet {
  //     display: grid;
  //   }
  // }

  &--desktop {
    display: block;
    @media (min-width: 1023px) {
      display: grid;
    }
  }

  &--tablet {
    display: block;
    @media (min-width: 744px) {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through $gridColTablet {
      @for $end from $start through $gridColTablet {
        &--tablet-#{$start}-#{$end} {
          @include onTablet {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
